      <v-divider vertical class="ml-n1"></v-divider>
      <v-col cols="12" lg="3" md="6" sm="12" xs="12">
        <v-card elevation="0">
          <v-card-text :class="`${$vuetify.theme.dark ? '': 'black--text'}`">
            <v-row class="mb-2">
