                  :btnTooltip="descriptions.TEXT_CASE"
                  :onClick="() => {}"
                />
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="9" class="text-left">
                <v-autocomplete
                  @change="onSave"
                  v-model="setting.caseType"
                  :items="caseTypes"
